// COLORS
$color-black: #000;
$color-white: #fff;
$color-text: #323232;
$color-ac—Åent: #f78434;
$color-bg: #58bebf;
$color-grey: #bbb;



@mixin line-clamp($lines) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
}

@mixin burger {
  position: relative;
  border: none;
  padding: 0;
  width: 40px;
  height: 20px;
  color: #000;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - 2px);
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}

@mixin desktop {
  @media (min-width: (1199px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (991px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (767px)) {
    @content;
  }
}

@mixin middle {
  @media (max-width: (575px)) {
    @content;
  }
}

@mixin mini {
  @media (max-width: (320px)) {
    @content;
  }
}
